@import "_BV_variables";

.content {
	// Bootstrap Customizations
	.container-fluid {
		text-align: center;

		background-color: $brightest;

		border: 1px solid rgba(0, 0, 0, 0.2);

		padding: 50px 140px; } }

.book-info {
	display: grid;
	grid-template-rows: max-content auto;
	grid-template-areas: "book-section" "reviews";
	grid-row-gap: 40px; }

.book-section {
	grid-area: book-section;

	display: grid;
	grid-template-columns: max-content auto;
	grid-template-areas: "cover-section details-section";
	grid-column-gap: 20px; }

.details-section {
	grid-area: details-section;

	display: grid;
	grid-template-rows: max-content max-content max-content max-content max-content max-content;
	grid-template-areas: "book-title" "book-rating" "book-author" "book-year" "book-isbn" "book-description"; }

.cover-section {
	grid-area: cover-section; }

.book-cover {
	img {
		height: 400px;

		border: 1px solid rgba(0, 0, 0, 0.1);
		border-bottom: 0px; } }

.book-title {
	margin-top: 0px;

	font-size: 1.7em;
	font-weight: normal; }

.reviewed {
	font-size: 0.5em;
	letter-spacing: 1px;
	color: $logo-color;

	.reviewed-icon {
		background-color: $logo-color;

		padding: 11px;

		border-radius: 50%;

		font-size: 1.5em;
		color: $brightest; } }

.book-author {
	margin-top: 0px; }

.book-year,
.book-author {
	font-size: .9em; }

.book-isbn {
	font-size: .8em;

	opacity: 0.5; }

.book-rating {
	font-size: .8em;

	display: flex;

	justify-content: space-between;

	margin-bottom: -5px; }

.ratings-count {
	display: block; }

.bookviews-rating {
	color: $logo-color; }

.goodreads-rating {
	color: $goodreads-logo-color;

	img {
		float: left;

		vertical-align: baseline;

		height: 24px;

		margin-right: 5px; } }

.book-description {
	grid-area: book-description;
	margin-top: 20px;

	align-self: end;

	//font-size: .9em

	p {
		text-align: justify; } }

.reviews {
	margin-bottom: -20px; }

.reviews-heading {
	margin-bottom: 10px; }

.review {
	background-color: $bright;

	border: 1px solid rgba(0, 0, 0, 0.2);
	border-radius: 7px;

	padding: 17px 20px;
	//padding-bottom: 17px

	margin: 0px -20px;

	margin-bottom: -7px;

	text-align: justify;

	font-size: 0.8em;

	box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.2);

	.stars {
		float: left;

		margin-left: 10px;

		font-size: 0.9em;

		color: $logo-color; } }

.reviewer-name {
	font-weight: bold; }

.review-body {
	word-break: break-word; }

.review-date {
	font-weight: normal;
	opacity: 0.7; }

.dropdown-menu-right {
	transform: none !important;
	top: unset !important;
	right: -21px !important; }

///////////////////////////////////////////////////////////////////////////////
// Media Queries

// Up to 1024px
@media (max-width: 1024px) {
	.content {
		padding: 5px 0px;

		.container-fluid {
			padding: 40px 80px; } } }

// Up to 768px
@media (max-width: 768px) {
	.content {
		padding: 0px;

		.container-fluid {
			padding: 20px 40px;
			padding-bottom: 40px;

			border-top: none; } }

	.book-cover {
		img {
			height: 300px; } } }

// Up to 534px
@media (max-width: 534px) {
	.book-section {
		grid-template-areas: "cover-section details-section" "book-description book-description";
		grid-template-rows: max-content max-content; }

	.book-cover {
		img {
			height: 250px; } }

	.book-info {
		grid-row-gap: 20px; }

	.details-section {
		grid-template-areas: "book-title" "book-rating" "book-author" "book-year" "book-isbn";
		grid-template-rows: max-content max-content max-content max-content max-content;

		grid-row-gap: 10px;

		font-size: 90%; }

	.book-rating {
		display: grid;
		grid-row-gap: 10px;
		margin-top: -10px; }

	.book-description {
		display: none; } }


// Up to 425px
@media (max-width: 425px) {
	.content {
		.container-fluid {
			padding: 20px;
			padding-bottom: 40px; } }

	.book-cover {
		img {
			height: 250px; } }

	.book-title {
		margin-top: -4px;

		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis; }

	.book-author,
	.book-year {
		b::after {
			content: "\a";
			white-space: pre; } } }

// Up to 375px
@media (max-width: 375px) {
	.book-section {
		grid-column-gap: 13px; }

	.book-cover {
		img {
			height: 220px; } }

	.details-section {
		font-size: 80%; }

	.book-title {
		margin-top: -4px; } }

// Up to 320px
@media (max-width: 320px) {
	.page-container {
		.header {
			padding: 0px 10px;

			.search-bar-container {
				.form-inline {
					margin: 0px -10px; } } }

		.content {
			.container-fluid {
				padding: 10px; } } }

	.book-section {
		grid-column-gap: 10px; }

	.review {
		margin: 0px -10px;
		font-size: .7em; }

	.dropdown-menu-right {
		right: -11px !important; } }
